.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Amatic+SC&family=Caveat:wght@600&family=Kanit:wght@300;500&family=Macondo&family=Ubuntu&display=swap');

*{
    margin: 0;
    padding: 0;
}

html{
    scroll-behavior: smooth;
}

.navbar{
    display: flex;
    justify-content: center;
    align-items: center;
    position: sticky;
    top: 0;
    
    cursor: pointer;
    /* background-color: orange; */
}

.background{
    background: url('./images/background.jpg');
    background-size: cover;
    background-blend-mode: darken;
}

/* ------------------------------------------------------------------------------- */

.leftnav{
    display: flex;
    width: 70%;
    /* background-color: black; */
    align-items: center;
}
.leftnav li{
    display: flex;
    list-style: none;
    padding: 24px 30px;

}
.leftnav li a{
    display: flex;
    text-decoration: none;
    font-size: 20px;
    font-family: 'Ubuntu', sans-serif;
    color: white;
}
.leftnav li a:hover{
    color: grey;
}


/* --------------------------------------------------------------------------------- */
.logo{
    display: flex;
    width: 20%;
    justify-content: center;
    align-items: center;
    /* background-color: brown; */
}
.logo img{
    width: 50px;
    border: 3px solid white;
    border-radius: 50%;
    /* padding: 20px; */

}

/* -------------------------------------------------------------------------------- */

.rightnav{
    width: 30%;
    /* background-color: silver; */
    text-align: right;
    margin-right: 50px;
}
#searchme{
    padding: 5px 20px;
    font-size: 16px;
    border-radius: 50px;
}




/* --------------------------------------------------------------------------------- */



.firstSection{
    height: 100vh;
}

.box-main{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    max-width: 85%;
    color: white;
    height: 75%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* --------------------------------------------------------------------- */

.firstHalf{
    display: flex;
    flex-direction: column;
    width: 50%;
    justify-content: center;
}

.text-big{
    font-size: 40px;
    font-family: 'Kanit', sans-serif;
}

.text-small{
    font-size: 20px;
    text-align: justify;
    margin-bottom: 10px;
}

.btn.btn-sm {
    padding: 5px 10px;
    vertical-align: middle;
    font-size: 14px;
    background-color: black;
}

.btn {
    padding: 6px 20px;
    margin: 10px 5px;
    font-size: 18px;
    border: 2px solid whitesmoke;
    border-radius: 20px;
    background: none;
    color: white;
    font-family: 'Ubuntu', sans-serif;
    cursor: pointer;
}

.btn:hover{
    background: white;
    color: black;
}

.btn-sm:hover{
    color: rgb(2, 3, 68);
    cursor: pointer;
}



/* ---------------------------------------------------------------------- */

.secondHalf{
    display: flex;
    width: 50%;
    justify-content: center;
}
.secondHalf img{
    width: 150px;
    height: 150px;
    border: 2px solid white;
    border-radius: 150px;
    display: block;
    margin: auto;
}

/* ------------------------------------------------------------------------- */



.section{
    display: flex;
    height: 550px;
    align-items: center;
    justify-content: center;
    max-width: 85%;
    margin: auto;
}

.thumbnail{
    display: flex;
    width: 22%;
    padding-top: 90px;
    justify-content: center;
    align-items: center;
}

.thumbnail img{
    width: 80%;
    height: 167px;
    border: 2px solid black;
    border-radius: 50px;
    display: block;
    margin: auto;
}

.paras{
    display: flex;
    flex-direction: column;
    width: 80%;
}

.sectionTag{
    display: flex;
    justify-content: center;
    font-family: 'Kanit', sans-serif;
    padding: 17px 0px;
}

.sectionSubTag {
    margin-bottom: 0px;
}
.lst-sec{
    margin-bottom: 50px;
    padding-top: 50px;
}

/* ------------------------------------------------------------------------------------- */

.lefty-section{
    /* baki traits section se mil hi rhe hai pehle se to srf ulta karne ke liye add kiya h */
    flex-direction: row-reverse;
    padding-bottom: 90px;
}



/* ----------------------------------------------------------------------------------------------- */


.contacts{
    background-color: #f2f2f3;
    color: black;
    height: 90vh;
}

.contact-us{
    text-align: center;
    padding-top: 35px;
    padding-bottom: 10px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;
}
.form{
    max-width: 830px;
    margin: 10px auto;
}
.form-input{
    margin: 5px 0px;
    width: 100%;
    padding: 3px 10px;
    font-size: 17px;
    border: 2px solid grey;
    border-radius: 9px;
}
.submitbtn{
    border: 2px solid black;
    color: black;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    background-color: #d3dada;
    border-radius: 8px;
}
.submitbtn:hover{
    background-color: white;
}



/* -------------------------------------------------------------------------------------------- */

.footer-text{
    padding: 20px;
    text-align: center;
    font-size: 18px;
    font-family: 'Ubuntu', sans-serif;
    background-color: rgba(131, 49, 128, 0.658);
    color: black;
}

.burger{
    display: none;
    position: absolute;
    cursor: pointer;
    right: 5%;
    top: 10px;
}
.line{
    width: 35px;
    background-color: white;
    height: 3px;
    margin: 5px 5px;
}


/* -============================================================================================================================== */


@media only screen and (max-width: 1150px){


    html,body{
        width: 100%;
        height: 100%;
        margin: 0px;
        padding: 0px;
        overflow-x: hidden;
    }


    .navbar{
        flex-direction: column;
    }
    .logo{
        width: 10%;
    }
    .leftnav{
        justify-content: center;
        width: 100%;
    }
    .rightnav{
        display: flex;
        width: 100%;
        margin: auto;
        margin-bottom: .9375rem;
        justify-content: center;
        align-items: center;
        padding-left: 5%;
    }

    .buttons{
        display: flex;
        justify-content: initial;
    }

    .thumbnail{
        padding: 20px;
    }
    .thumbnail img{
        width: 150px;
        height: 150px;
    }
    .box-main{
        width: 100%;
    }

    .firstSection{
        height: 85%;
    }

    .fst-sec{
        flex-direction: column-reverse;
        height: auto;
        padding-top: 80px;
    }

    .lefty-section{
        flex-direction: column-reverse;
        height: auto;
        padding-bottom: 0;
    }

    .lst-sec{
        flex-direction: column-reverse;
        height: auto;
    }

    .section{
        padding-bottom: 50px;
        max-width: 100%;
    }

    .form{
        max-width: 65%;
    }

    .contact-us{
        font-size: 25px
    }

    .submitbtn{
        font-size: 85%;
    }


    .footer-text{
        font-size: 90%;
        padding: 1% 0px;
    }
}



/* ===================================================================================================================================== */


@media only screen and (max-width: 700px){


    
    .navbar{
        flex-direction: column;
        position: static;
        height: 400px;
        cursor: pointer;
        transition:all 0.9s ease-in-out;
    }

    .burger{
        display: block;
    }

    .h-nav-resp{
        height: 72px;
    }
    .v-nav-resp{
        visibility: hidden;
    }

    

    .pic-background{
        background-color: rgba(0, 0, 0, 0.784);
    }

    .leftnav{
        flex-direction: column;
    }

    .leftnav li{
        padding: 15px 0px;
    }

    .logo{
        padding: 15px 0px;
    }

    .rightnav{
        flex-direction: column;
        margin-left: -22px;
    }




    .section{
        max-width: 100%;
    }

    .firstSection{
        height: 100%;
    }
    .box-main{
        flex-direction: column-reverse;
        height: 740px;
    }   

    .buttons{
        display: flex;
        justify-content: center;
    }

    .secondHalf{
        width: 80%;
    }

    .firstHalf{
        width: 100%;
    }

    .paras{
        width: 95%;
    }

    .secondHalf img{
        width: 100px;
        height: 100px;
        margin-top: 150px;
        margin-bottom: 20px;
    }

    .text-big{
        font-size: 30px;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
        text-align: center;
        margin-bottom: 30px;
    }
}
